@import url("https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import "variables";
@import "headlines";
@import "navbarStyles";
@import "headerStyles";
@import "aboutStyles";
@import "footerStyles";
@import "projectsPage";
@import "projectCard";

body {
  font-family: "Montserrat Alternates", sans-serif;;
  font-weight: 400;
  font-style: normal;
  min-height: 100vh;
}

body.dark-theme {
  background-color: $background-color-dark;
  color: $text-color-dark;
}

#root {
  min-height: 100vh;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

@mixin theme($primary-color, $background-color, $text-color, $accent-color, $secondary-color) {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: $primary-color;
  }
  .logo {
    color: $text-color;
  }
  .secondary_text {
    color: $secondary-color;
    font-weight: 600;
  }
  .logo_container::before {
    background-color: $secondary-color;
  }
  a {
    color: $primary-color;
    padding: 7px;
    border-radius: 10px;
  }
  a:hover {
    background-color: $accent-color;
  }

  button {
    background-color: $accent-color;
    color: $text-color;
  }
  .wrapper-project-card{
    background-color: $background-color;
    color: $text-color;
    box-shadow: 10px 10px 10px $primary-color
  }
}

.light-theme {
  @include theme(
    $primary-color-light,
    $background-color-light,
    $text-color-light,
    $accent-color-light,
    $secondary-color-light
  );
}

.dark-theme {
  @include theme(
    $primary-color-dark,
    $background-color-dark,
    $text-color-dark,
    $accent-color-dark,
    $secondary-color-dark
  );
}

body,
a,
button {
  transition: all 0.3s;
}
