@import url("https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
}

h1 {
  font-size: 36px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 24px;
}

h4 {
  font-size: 20px;
}

h5 {
  font-size: 16px;
}

h6 {
  font-size: 14px;
}

.nav {
  padding: 25px;
  font-size: 20px;
}
.nav .logo_container {
  width: fit-content;
  margin-left: 5%;
  transition: all 0.3s;
  position: relative;
  display: inline-block;
}
.nav .logo_link {
  color: #222222;
  width: fit-content;
}
.nav .logo_link:hover {
  background-color: rgba(255, 255, 255, 0);
}
.nav .logo_container::before {
  content: "";
  position: absolute;
  width: 100px;
  height: 10px;
  background-color: #b0b0b0;
  border-radius: 50%;
  transform: translate(-5%, 100%) scale(0);
  transition: transform 0.5s ease;
  z-index: -1;
}
.nav .logo_container:hover::before {
  transform: translate(0%, 100%) scale(1);
}
.nav .logo {
  width: fit-content;
  display: block;
  position: relative;
  transition: transform 0.5s ease;
  z-index: 1;
}
.nav .logo:hover {
  transform: translateY(-5px);
}
.nav .nav_list_item {
  min-width: fit-content;
}
.nav .nav_list_item_active {
  text-decoration: underline;
  text-underline-offset: 10px;
}

.container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  flex-direction: row;
}
.container ul {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.theme_btn {
  order: 9;
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 51px;
  height: 27px;
  padding: 5px;
  border-radius: 50px;
}
.theme_btn img {
  width: 40%;
  height: auto;
}

.theme_btn::before {
  content: "";
  position: absolute;
  top: 1px;
  left: 1px;
  display: block;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #fff;
  transition: left 0.2s ease-in;
}

.theme_btn-active::before {
  left: 24px;
}

.dark_theme_btn_icon {
  position: relative;
  z-index: 9;
}

.main_header {
  width: 100%;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main_header .intro {
  flex-wrap: wrap;
  width: 50%;
  font-size: 30px;
}

.about {
  padding: 30px;
  display: grid;
  grid-template-columns: 1.7fr 1.3fr;
}
.about .avatar img {
  width: 512px;
  height: auto;
  border-radius: 10px;
}
.about .skills {
  margin-top: 10px;
}
.about .skills ul {
  margin-top: 3%;
  margin-left: 7%;
  font-size: 20px;
}
.about .skills ul li span {
  font-family: "Courier New", sans-serif;
}
.about .skills ul li img {
  width: 25px;
  height: auto;
}
.about .skills .skills_list, .about .skills .skills_list li {
  list-style: disc;
}
.about .skills .about_me_list {
  margin-bottom: 5%;
}
.about .skills .about_me_list, .about .skills .about_me_list li {
  list-style-type: "-";
}

footer {
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 20vh;
  margin-top: auto;
  transition: all 0.3s;
}
footer .contactLinks {
  display: flex;
  align-self: center;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 5px;
  width: 35%;
}
footer img {
  width: 32px;
  height: auto;
}

.highlited {
  width: 99%;
  align-self: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: 0px -5px 10px #E6E6FA;
}

.projects_main {
  margin-left: 4%;
  margin-top: 3%;
  display: grid;
  row-gap: 10%;
  grid-template-columns: 1fr 1fr 1fr;
}

.wrapper-project-card {
  list-style-type: none;
  width: 65%;
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  border: 1px solid gray;
}
.wrapper-project-card .preview {
  overflow: hidden;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}
.wrapper-project-card .project-info {
  margin: 5% 5%;
  align-self: flex-start;
}
.wrapper-project-card .project-info .skills {
  margin-top: 3%;
  font-size: 12px;
}
.wrapper-project-card .project-info .github-link {
  display: block;
  text-decoration: none;
  color: black;
}
.wrapper-project-card .project-info .github-link:hover {
  transform: translateY(5px);
  cursor: pointer;
  font-size: larger;
}

body {
  font-family: "Montserrat Alternates", sans-serif;
  font-weight: 400;
  font-style: normal;
  min-height: 100vh;
}

body.dark-theme {
  background-color: #121212;
  color: #E0E0E0;
}

#root {
  min-height: 100vh;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.light-theme h1,
.light-theme h2,
.light-theme h3,
.light-theme h4,
.light-theme h5,
.light-theme h6 {
  color: #8A2BE2;
}
.light-theme .logo {
  color: #222222;
}
.light-theme .secondary_text {
  color: #b0b0b0;
  font-weight: 600;
}
.light-theme .logo_container::before {
  background-color: #b0b0b0;
}
.light-theme a {
  color: #8A2BE2;
  padding: 7px;
  border-radius: 10px;
}
.light-theme a:hover {
  background-color: #E6E6FA;
}
.light-theme button {
  background-color: #E6E6FA;
  color: #222222;
}
.light-theme .wrapper-project-card {
  background-color: #FAFAFA;
  color: #222222;
  box-shadow: 10px 10px 10px #8A2BE2;
}

.dark-theme h1,
.dark-theme h2,
.dark-theme h3,
.dark-theme h4,
.dark-theme h5,
.dark-theme h6 {
  color: #6A0DAD;
}
.dark-theme .logo {
  color: #E0E0E0;
}
.dark-theme .secondary_text {
  color: #c9c9c9;
  font-weight: 600;
}
.dark-theme .logo_container::before {
  background-color: #c9c9c9;
}
.dark-theme a {
  color: #6A0DAD;
  padding: 7px;
  border-radius: 10px;
}
.dark-theme a:hover {
  background-color: #D8BFD8;
}
.dark-theme button {
  background-color: #D8BFD8;
  color: #E0E0E0;
}
.dark-theme .wrapper-project-card {
  background-color: #121212;
  color: #E0E0E0;
  box-shadow: 10px 10px 10px #6A0DAD;
}

body,
a,
button {
  transition: all 0.3s;
}

