footer {
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 20vh;
  margin-top: auto;
  transition: all 0.3s;
  .contactLinks {
    display: flex;
    align-self: center;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 5px;
    width: 35%;
  }
  img {
    width: 32px;
    height: auto;
  }
}

.highlited {
  width: 99%;
  align-self: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: 0px -5px 10px $accent-color-light;
}
